.loginWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}
h1{
    text-shadow: 0px 5px 5px rgb(172, 172, 172)
}
.submitButton{
    background-color: white;
    border-radius: 15px;
    padding: 3px;
    width: 100%;
    margin-top:15px;
    font-size:normal;
    box-shadow: 0px 5px 5px rgb(172, 172, 172) ;
    border: 1px solid black;

}
.submitButton:hover{
    background-color: #624AF2;
    ;
    border-radius: 15px;
    padding: 5px;
    width: 100%;
    margin-top:15px;
    box-shadow: none;
}
input[type="text"]{
    border-radius: 15px;
    border: 1px solid black;
    padding-left: 5px;
    padding-right: 5px;
    box-shadow: 0px 5px 5px rgb(172, 172, 172) ;
} 
input[type="password"]{
    border-radius: 15px;
    border: 1px solid black;
    padding-left: 5px;
    padding-right: 5px;
    box-shadow: 0px 5px 5px rgb(172, 172, 172) ;
} 
input:focus{
    outline: none;
}
input[type="radio"]{
    margin-top: 20px;

}
.loginWrapper select{
    width: 100%;
    border-radius: 15px;
    border: 1px solid black;
    box-shadow: 0px 5px 5px rgb(172, 172, 172) ;

}